body {
    background: linear-gradient(to top, #4facfe, #00f2fe);
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 40px;
    padding-bottom: 40px;
}

.object {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    .timeline-title {
        color: #ffffff;
    }
}
