@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: inherit;
}

ul,
ol {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: normal;
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

body {
  line-height: 1;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

picture,
video,
canvas,
svg {
  max-width: 100%;
  display: block;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img {
  max-width: 100%;
}

*, html, body {
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
}

h1 {
  font-size: 48px;
}

h2 {
  font-size: 36px;
}

h3 {
  font-size: 28px;
}

h4 {
  font-size: 24px;
}

h5 {
  font-size: 22px;
}

h6 {
  font-size: 20px;
}

body {
  background: linear-gradient(to top, #4facfe, #00f2fe);
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 40px;
  padding-bottom: 40px;
}

.object {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.object .timeline-title {
  color: #ffffff;
}

.header h1 {
  color: #ffffff;
  margin-bottom: 30px;
}

.timeline {
  background-image: url("../images/timeline.png");
  background-repeat: repeat-y;
  background-position: center;
  min-height: 300px;
  margin-top: 40px;
}
.timeline .timeline-controls {
  text-align: center;
}

.timeline-container {
  background-color: #ffffff;
  width: 700px;
  border-radius: 40px;
  padding: 40px;
  margin-top: 40px;
  position: relative;
  border: 1px solid #3d5af1;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2), 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
}
@media only screen and (max-width: 750px) {
  .timeline-container {
    width: 500px;
  }
}
@media only screen and (max-width: 550px) {
  .timeline-container {
    width: 100%;
  }
}
.timeline-container:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 20px rgba(0, 0, 0, 0.3), 0 6px 8px rgba(0, 0, 0, 0.2);
}
@media only screen and (max-width: 750px) {
  .timeline-container:hover {
    transform: unset;
  }
}
.timeline-container .timeline-controls {
  text-align: center;
}
.timeline-container .ellipse1 {
  position: absolute;
  top: 0;
  left: -40px;
  animation: float 3s ease-in-out infinite;
}
@media only screen and (max-width: 920px) {
  .timeline-container .ellipse1 {
    display: none;
  }
}
.timeline-container .ellipse2 {
  position: absolute;
  bottom: 0;
  right: -100px;
  animation: float 3s ease-in-out infinite;
  animation-delay: 1.5s;
}
@media only screen and (max-width: 920px) {
  .timeline-container .ellipse2 {
    display: none;
  }
}

@keyframes float {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}
.event {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  background-image: url("../images/bullet.svg");
  background-repeat: no-repeat;
  background-position: center;
  gap: 24px;
  margin-top: 20px;
}
@media only screen and (max-width: 550px) {
  .event {
    flex-direction: column;
    background-color: #fff;
    background-image: none;
    border: 2px solid #3d5af1;
    padding: 10px;
    text-align: center;
  }
}
.event.right .date {
  color: #3d5af1;
  font-weight: 700;
  font-size: 20px;
  padding-left: 30px;
  flex: 1;
  display: flex;
  justify-content: flex-start;
  background-image: url("../images/connection.png");
  background-repeat: no-repeat;
  background-position: center left;
  order: 2;
}
@media only screen and (max-width: 550px) {
  .event.right .date {
    background-image: none;
    padding-left: 0;
    display: block;
    order: unset;
  }
}
.event.right .content {
  padding-right: 30px;
  flex: 1;
  display: flex;
  justify-content: flex-end;
  background-image: url("../images/connection.png");
  background-repeat: no-repeat;
  background-position: center right;
  order: 1;
}
@media only screen and (max-width: 550px) {
  .event.right .content {
    background-image: none;
    display: block;
    padding-right: 0;
    order: unset;
  }
}
.event .date {
  color: #3d5af1;
  font-weight: 700;
  font-size: 20px;
  padding-right: 30px;
  flex: 1;
  display: flex;
  justify-content: flex-end;
  background-image: url("../images/connection.png");
  background-repeat: no-repeat;
  background-position: center right;
  order: 1;
}
@media only screen and (max-width: 550px) {
  .event .date {
    background-image: none;
    padding-right: 0;
    display: block;
    order: unset;
  }
}
.event .content {
  padding-left: 30px;
  flex: 1;
  display: flex;
  justify-content: flex-start;
  background-image: url("../images/connection.png");
  background-repeat: no-repeat;
  background-position: center left;
  order: 2;
}
@media only screen and (max-width: 550px) {
  .event .content {
    background-image: none;
    display: block;
    padding-left: 0;
    order: unset;
  }
}
.event .content .title {
  color: #222222;
}
.event .content p {
  font-size: 0.8em;
  margin-top: 10px;
  margin-bottom: 10px;
  color: #6d7385;
}

.btn {
  border-radius: 8px;
  padding: 10px 10px;
  cursor: pointer;
  transition: 300ms;
  border: 1px solid transparent;
  font-weight: 700;
  font-size: 0.8em;
  color: 222222;
  margin-top: 10px;
}
.btn:disabled {
  opacity: 0.5;
  color: #222222;
  cursor: not-allowed;
}
.btn.danger {
  color: #f86494;
}
.btn.danger:disabled {
  opacity: 0.5;
  color: #222222;
}
.btn.danger:hover {
  border: 1px solid #f86494;
}
.btn.primary {
  color: #6e84f4;
  margin-right: 10px;
}
.btn.primary:disabled {
  opacity: 0.5;
  color: #222222;
}
.btn.primary:hover {
  border: 1px solid #6e84f4;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(16px) saturate(200%);
  -webkit-backdrop-filter: blur(16px) saturate(200%);
  background-color: rgba(17, 25, 40, 0.5);
  justify-content: center;
  align-items: center;
  opacity: 0;
  pointer-events: none;
  display: flex;
  transition: 300ms;
}
.modal.active {
  opacity: 1;
  pointer-events: auto;
}
.modal .modal-content {
  background: #fff;
  padding: 20px;
  border-radius: 5px;
  width: 300px;
  position: relative;
  padding: 40px;
}
.modal .modal-content p {
  color: #6d7385;
}
.modal .modal-content h3 {
  margin-bottom: 30px;
  color: #3d5af1;
}
.modal .modal-content label {
  font-size: 12px;
  color: #666666;
  font-weight: 700;
}
.modal .modal-content input, .modal .modal-content textarea {
  padding: 10px;
  border-radius: 4px;
  margin-bottom: 10px;
  border: 2px solid #ccc;
  width: 100%;
  outline: 0;
  font-size: 14px;
}
.modal .modal-content input:focus, .modal .modal-content textarea:focus {
  border: 2px solid #3d5af1;
}

.modal-alert {
  color: color;
  font-size: 12px;
  margin-top: 10px;
}